.gridsvg {
    padding: 0;
    margin: 0;
    display: block;
    overflow: hidden !important;
    position: relative;
}
.gridsvg * {
    margin: 0;
    padding: 0;
    overflow: hidden;
}
.viewport:active {
    cursor: move;
}
.dragover {
    background: rgba(255,0,255,.9)
}
.viewport {
    margin: 0;
    padding: 0;
    width: 100%;
    max-height: 100%;
    height: 100%;
    position: relative;
    display: inline-grid;
    grid-auto-rows: 1fr;
    grid-template-columns: minmax(0, 1fr);
    overflow: auto !important;
    justify-items: safe center;
    align-items: center;
    overflow: scroll;
}
